"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import {
    LayoutDashboard,
    Settings,
    LogOut,
    Menu,
    BarChart2,
    Activity,
    X
} from "lucide-react"
import Image from "next/image"
import { SettingsView } from "@/components/admin/settings-view"
import { AnalyticsView } from "@/components/admin/analytics-view"
import { LiveTraceView } from "@/components/admin/live-trace-view"

export default function AdminDashboard() {
    const [activeTab, setActiveTab] = useState("dashboard")
    const [sidebarOpen, setSidebarOpen] = useState(false)
    const router = useRouter()

    useEffect(() => {
        // Check for auth token
        const token = document.cookie.split("; ").find((row) => row.startsWith("admin_token="))
        if (!token) {
            router.push("/admin/login")
        }
    }, [router])

    const menuItems = [
        { id: "dashboard", icon: LayoutDashboard, label: "Dashboard" },
        { id: "analytics", icon: BarChart2, label: "Analytics" },
        { id: "live-trace", icon: Activity, label: "User Trace" },
        { id: "settings", icon: Settings, label: "Settings" },
    ]

    const handleLogout = () => {
        document.cookie = "admin_token=; path=/; expires=Thu, 01 Jan 1970 00:00:01 GMT"
        router.push("/admin/login")
    }

    const renderContent = () => {
        switch (activeTab) {
            case "dashboard":
                return <AnalyticsView /> // Reusing Analytics view as main dashboard for now, or could be a summary
            case "analytics":
                return <AnalyticsView />
            case "live-trace":
                return <LiveTraceView />
            case "settings":
                return <SettingsView />
            default:
                return <AnalyticsView />
        }
    }

    return (
        <div className="min-h-screen bg-gray-50 flex font-sans">
            {/* Mobile Sidebar Backdrop */}
            {sidebarOpen && (
                <div
                    className="fixed inset-0 bg-black/50 z-40 lg:hidden backdrop-blur-sm transition-opacity"
                    onClick={() => setSidebarOpen(false)}
                />
            )}

            {/* Sidebar */}
            <aside
                className={`fixed lg:static inset-y-0 left-0 z-50 w-72 bg-white border-r border-gray-200 transform transition-transform duration-300 ease-in-out lg:transform-none flex flex-col ${sidebarOpen ? "translate-x-0" : "-translate-x-full"
                    }`}
            >
                <div className="h-24 flex items-center justify-center border-b border-gray-100 px-6 relative">
                    <div className="flex items-center gap-2">
                        <Image
                            src="/images/cropped-ixtzd985wxrrucij9vkr.avif"
                            alt="CADLINK"
                            width={140}
                            height={40}
                            className="object-contain"
                        />
                    </div>
                    <button
                        onClick={() => setSidebarOpen(false)}
                        className="absolute right-4 lg:hidden text-gray-400 hover:text-gray-600"
                    >
                        <X className="w-6 h-6" />
                    </button>
                </div>

                <nav className="flex-1 p-6 space-y-2 overflow-y-auto">
                    <div className="text-xs font-bold text-gray-400 uppercase tracking-wider mb-4 px-4">Menu</div>
                    {menuItems.map((item) => {
                        const isActive = activeTab === item.id
                        return (
                            <button
                                key={item.id}
                                onClick={() => {
                                    setActiveTab(item.id)
                                    setSidebarOpen(false)
                                }}
                                className={`w-full flex items-center gap-3 px-4 py-3.5 rounded-xl text-sm font-bold transition-all duration-200 group ${isActive
                                        ? "bg-[#0168A0] text-white shadow-lg shadow-[#0168A0]/30"
                                        : "text-gray-600 hover:bg-gray-50 hover:text-gray-900"
                                    }`}
                            >
                                <item.icon className={`w-5 h-5 ${isActive ? "text-white" : "text-gray-400 group-hover:text-gray-600"}`} />
                                {item.label}
                            </button>
                        )
                    })}
                </nav>

                <div className="p-6 border-t border-gray-100">
                    <button
                        onClick={handleLogout}
                        className="flex items-center gap-3 px-4 py-3.5 w-full rounded-xl text-sm font-bold text-red-600 hover:bg-red-50 transition-colors"
                    >
                        <LogOut className="w-5 h-5" />
                        Logout
                    </button>
                </div>
            </aside>

            {/* Main Content */}
            <div className="flex-1 flex flex-col min-w-0 h-screen overflow-hidden">
                {/* Top Header */}
                <header className="bg-white border-b border-gray-200 h-20 flex items-center justify-between px-4 lg:px-8 flex-shrink-0">
                    <div className="flex items-center gap-4">
                        <button
                            onClick={() => setSidebarOpen(true)}
                            className="lg:hidden p-2 text-gray-600 hover:bg-gray-100 rounded-lg transition-colors"
                        >
                            <Menu className="w-6 h-6" />
                        </button>
                        <h1 className="text-xl font-black text-gray-900 capitalize hidden sm:block">
                            {menuItems.find(i => i.id === activeTab)?.label}
                        </h1>
                    </div>

                    <div className="flex items-center gap-4">
                        <div className="flex items-center gap-3 pl-6 border-l border-gray-100">
                            <div className="text-right hidden sm:block">
                                <p className="text-sm font-bold text-gray-900">Admin User</p>
                                <p className="text-xs text-gray-500">Super Admin</p>
                            </div>
                            <div className="w-10 h-10 bg-[#0168A0] rounded-full flex items-center justify-center text-white font-bold shadow-md shadow-[#0168A0]/20">
                                A
                            </div>
                        </div>
                    </div>
                </header>

                {/* Page Content */}
                <main className="flex-1 overflow-y-auto bg-gray-50 p-4 lg:p-8">
                    <div className="max-w-7xl mx-auto">
                        {renderContent()}
                    </div>
                </main>
            </div>
        </div>
    )
}
