"use client"

import { useState, useEffect } from "react"
import { MapPin, Users, Globe, Clock } from "lucide-react"

type Visitor = {
    id: string
    country: string
    city: string
    page: string
    duration: string
    status: "active" | "idle"
    coordinates: { x: number; y: number }
}

export function LiveTraceView() {
    const [visitors, setVisitors] = useState<Visitor[]>([])

    // Simulate live data updates
    useEffect(() => {
        const initialVisitors: Visitor[] = [
            { id: "1", country: "USA", city: "New York", page: "/product", duration: "2m 30s", status: "active", coordinates: { x: 25, y: 35 } },
            { id: "2", country: "Germany", city: "Berlin", page: "/", duration: "45s", status: "active", coordinates: { x: 52, y: 28 } },
            { id: "3", country: "Japan", city: "Tokyo", page: "/blog/automation", duration: "5m 12s", status: "idle", coordinates: { x: 85, y: 38 } },
            { id: "4", country: "Brazil", city: "SÃ£o Paulo", page: "/checkout", duration: "1m 10s", status: "active", coordinates: { x: 32, y: 75 } },
            { id: "5", country: "UK", city: "London", page: "/", duration: "10s", status: "active", coordinates: { x: 48, y: 25 } },
        ]
        setVisitors(initialVisitors)

        const interval = setInterval(() => {
            setVisitors((prev) => {
                // Randomly update status or add/remove visitors to simulate live activity
                return prev.map(v => ({
                    ...v,
                    status: Math.random() > 0.8 ? (v.status === "active" ? "idle" : "active") : v.status,
                    duration: v.status === "active" ? updateDuration(v.duration) : v.duration
                }))
            })
        }, 2000)

        return () => clearInterval(interval)
    }, [])

    const updateDuration = (duration: string) => {
        // Simple helper to increment duration string (very basic)
        return duration // In a real app, we'd parse and increment
    }

    return (
        <div className="space-y-8 animate-in fade-in duration-500">
            <div className="flex items-center justify-between">
                <div>
                    <h2 className="text-2xl font-black text-gray-900">Live User Trace</h2>
                    <p className="text-gray-500">Real-time monitoring of active visitors on your site.</p>
                </div>
                <div className="flex items-center gap-2 bg-green-50 text-green-700 px-4 py-2 rounded-full text-sm font-bold animate-pulse">
                    <div className="w-2 h-2 bg-green-500 rounded-full" />
                    {visitors.filter(v => v.status === "active").length} Active Users
                </div>
            </div>

            <div className="grid lg:grid-cols-3 gap-8">
                {/* Map Visualization (Placeholder) */}
                <div className="lg:col-span-2 bg-[#0a192f] rounded-2xl p-6 shadow-xl relative overflow-hidden min-h-[400px]">
                    <div className="absolute inset-0 opacity-20 bg-[url('https://upload.wikimedia.org/wikipedia/commons/e/ec/World_map_blank_without_borders.svg')] bg-contain bg-no-repeat bg-center" />

                    {/* Simulated User Dots */}
                    {visitors.map((visitor) => (
                        <div
                            key={visitor.id}
                            className="absolute w-4 h-4 transform -translate-x-1/2 -translate-y-1/2 group cursor-pointer"
                            style={{ left: `${visitor.coordinates.x}%`, top: `${visitor.coordinates.y}%` }}
                        >
                            <div className={`w-3 h-3 rounded-full ${visitor.status === "active" ? "bg-green-500 shadow-[0_0_10px_#22c55e]" : "bg-yellow-500"} animate-pulse`} />

                            {/* Tooltip */}
                            <div className="absolute bottom-full left-1/2 -translate-x-1/2 mb-2 w-max bg-white text-gray-900 text-xs font-bold py-1 px-2 rounded shadow-lg opacity-0 group-hover:opacity-100 transition-opacity pointer-events-none z-10">
                                {visitor.city}, {visitor.country}
                            </div>
                        </div>
                    ))}

                    <div className="absolute bottom-6 left-6 text-white/50 text-xs">
                        Live Map Visualization
                    </div>
                </div>

                {/* User Log */}
                <div className="bg-white rounded-2xl border border-gray-100 shadow-sm overflow-hidden flex flex-col h-[400px]">
                    <div className="p-4 border-b border-gray-100 bg-gray-50">
                        <h3 className="font-bold text-gray-900 flex items-center gap-2">
                            <Clock className="w-4 h-4 text-gray-500" />
                            Activity Log
                        </h3>
                    </div>
                    <div className="flex-1 overflow-y-auto p-4 space-y-4">
                        {visitors.map((visitor) => (
                            <div key={visitor.id} className="flex items-start gap-3 p-3 rounded-xl hover:bg-gray-50 transition-colors border border-transparent hover:border-gray-100">
                                <div className={`mt-1 w-2 h-2 rounded-full flex-shrink-0 ${visitor.status === "active" ? "bg-green-500" : "bg-yellow-500"}`} />
                                <div className="flex-1 min-w-0">
                                    <div className="flex items-center justify-between mb-1">
                                        <span className="font-bold text-gray-900 text-sm truncate">{visitor.city}, {visitor.country}</span>
                                        <span className="text-xs text-gray-400">{visitor.duration}</span>
                                    </div>
                                    <div className="flex items-center gap-2 text-xs text-gray-500">
                                        <Globe className="w-3 h-3" />
                                        <span className="truncate">{visitor.page}</span>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <div className="bg-white p-6 rounded-2xl border border-gray-100 shadow-sm">
                    <div className="flex items-center gap-4">
                        <div className="p-3 bg-blue-50 rounded-xl text-[#0168A0]">
                            <Users className="w-6 h-6" />
                        </div>
                        <div>
                            <p className="text-sm text-gray-500 font-bold">Total Visits Today</p>
                            <p className="text-2xl font-black text-gray-900">1,234</p>
                        </div>
                    </div>
                </div>
                <div className="bg-white p-6 rounded-2xl border border-gray-100 shadow-sm">
                    <div className="flex items-center gap-4">
                        <div className="p-3 bg-purple-50 rounded-xl text-purple-600">
                            <MapPin className="w-6 h-6" />
                        </div>
                        <div>
                            <p className="text-sm text-gray-500 font-bold">Top Country</p>
                            <p className="text-2xl font-black text-gray-900">United States</p>
                        </div>
                    </div>
                </div>
                <div className="bg-white p-6 rounded-2xl border border-gray-100 shadow-sm">
                    <div className="flex items-center gap-4">
                        <div className="p-3 bg-orange-50 rounded-xl text-orange-600">
                            <Globe className="w-6 h-6" />
                        </div>
                        <div>
                            <p className="text-sm text-gray-500 font-bold">Active Pages</p>
                            <p className="text-2xl font-black text-gray-900">8</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
